.loginPage{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.loginForm{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap:10px;
    background-color: lavender;
    padding: 20px;
    padding-top: 25px;
    padding-bottom: 25px;
    border-radius: 20px;
}

.labelIp{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap:10px;
}

label{
    display: flex;
    justify-content: start;
    align-items: start;
}


button{
    background-color: blue;
    padding: 5px;
    border-radius: 10px;
    color: white;
}